@import url('basic.css');
@import url('https://cdn.jsdelivr.net/npm/bootstrap-icons@1.11.3/font/bootstrap-icons.min.css');

/* 變量設置 */
$bk1: #1d1d1d;
$bk2: #333;
$gr1: #e9e9e9;
$gr2: #7a7a7a;
$main: #baf13a;
$main2: #98d900;
$side: #a839ff;
$side2: #a839ff;

/* 全局容器設置 */
.cart-wrap-f {
  .container-f {
    margin: 0 auto;
    width: 100%;

    .cart-wrap {
      .container {
        max-width: 90%;
        margin: 0 auto;
        box-sizing: border-box;
      }

      /* 頂部樣式 */
      .top {
        margin-top: 5rem;

        h3 {
          color: $gr1;
          font-size: 2rem;
          font-weight: bold;
          padding-bottom: 2rem;
          border-bottom: 2px solid $main;
        }
      }

      /* 表格樣式 */
      .cart-border {
        margin-top: 20px;
        border: 2px solid $main;
        border-radius: 20px;
        padding-bottom: 20px;
        text-align: center;
        background-color: $bk2;
      }

      .cart-table {
        width: 100%;
        border-collapse: collapse;
        margin-top: 20px;

        th:nth-of-type(1) {
          width: 26%;
        }
        th:nth-of-type(2) {
          padding-left: 100px;
          width: 20%;
        }
        th:nth-of-type(3) {
          width: 15%;
        }
        th:nth-of-type(4) {
          width: 15%;
        }
        th:nth-of-type(5) {
          width: 15%;
        }

        tbody {
          font-size: 1.2rem;
        }

        td {
          padding: 10px 20px;
          text-align: left;
        }

    
      }

      .cart-border-inner {
        width: 100%;
        margin-top: 20px;
        border: 2px solid $main;
        border-radius: 20px;
        background-color: $bk2;

        .cart-item {
          margin: 0 auto;
          max-width: 95%;
          display: flex;
          align-items: center;
          justify-content: space-around;
          gap: 20px;
          padding-top: 20px;
          background-color: $gr2;
          border: 1px solid $main;
          border-radius: 10px;
          margin-top: 20px;
          margin-bottom: 20px;
          flex-wrap: nowrap;

          .cart-item-left-content {
            width: 100%;
            display: flex;

            img {
              display: inline-block;
              vertical-align: middle;
              object-fit: cover;
              width: 130px;
              height: 130px;
              padding-bottom: 20px;
            }

            .cart-item-content {
              display: flex;
              margin: 0;
              width: 100px;
              flex-direction: column;
              justify-content: center;
              margin-left: 10px;
              margin-right: 50px;
              h4 {
                font-size: 1.3rem;
              }

              h5 {
                font-size: 1rem;
              }

              h4,
              h5 {
                line-height: 30px;
                margin: 0;
                white-space: nowrap;
              }
            }
          }

          .price {
            margin: 0;
            width: 5%;
          }
          // .quantity-plus-minus {
          // }
          .total {
            margin: 0;
            width: 10%;
          }
          .delete {
            width: 0%;
          }

          .quantity-plus-minus {
            display: flex;
            align-items: center;
            gap: 5px;

            i {
              font-size: 1rem;
              color: $gr1;

              &:hover {
                color: $main;
                transition: 1s;
              }
            }
          }

          .delete i {
            font-size: 1.3rem;
            color: $gr1;

            &:hover {
              color: $main;
              transition: 1s;
            }
          }
        }
      }

      /* 固定底部樣式 */
      .checkButton {
        position: sticky;
        bottom: 0;
        margin-top: 18rem;
        max-width: 100%;
        height: 70px;
        background-color: #749f10;
        color: $gr1;
        border-radius: 5px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 0 20px;
        box-sizing: border-box;

        .rightCheckButton {
          display: flex;
          align-items: center;
          gap: 20px;

          a {
            text-decoration: none;
            color: $gr1;
            background-color: $main2;
            padding: 10px 30px;
            border-radius: 10px;

            &:hover {
              background-color: $gr1;
              color: $main2;
              transition: 1s;
            }
          }
          .GoBtn {
            border: none;
            color: $gr1;
            background-color: $main2;
            padding: 10px 30px;
            border-radius: 10px;
            cursor: pointer;
            &:hover {
              background-color: $gr1;
              color: $main2;
              transition: 1s;
            }
          }
        }

        @media (max-width: 480px) {
          flex-direction: column;
          height: auto;
          padding: 5px 0px;

          .rightCheckButton {
            gap: 10px;
          }
        }
      }
    }
  }
}
