body {
  font-family: Arial, sans-serif;
  color: #fff;
  background-color: #222;
  margin: 0;
  padding: 0;
  width: 100%;

  overflow-x: hidden;

  // 視差效果
  .parallax-window {
    min-height: 500px;
    width: 50%;
    background: transparent;
  }

  .header-nav {
    img {
      width: 100vw;
      height: 10vh;
      object-fit: cover;
    }
  }

  .intro {
    width: 100%;
    height: 100dvh;
    position: relative;
    display: flex;
    flex-direction: column;

    .skateImg {
      width: 40%;
      right: 20%;
      bottom: 50%;
      transform: rotate(20deg);
      position: absolute;
    }

    h1 {
      text-align: center;
      font-size: 7rem;
      letter-spacing: 0.3rem;
      margin-top: 26rem;
      line-height: 130px;
      // 這個可以調透明顏色
      // color: transparent;
      color: #b3e837;
      font-weight: bold;
      // -webkit-text-stroke: 2px #b3e837; // 外框顏色
    }

    // rwd
    @media (max-width: 1200px) {
      .skateImg {
        width: 45%;
        right: 10%;
        bottom: 40%;
        transform: rotate(20deg);
        position: absolute;
      }
      h1 {
        margin-top: 30rem;
        line-height: 80px;
        font-size: 4.5rem;
        flex-wrap: nowrap;
      }
    }

    @media (max-width: 767px) {
      .skateImg {
        width: 60%;
        right: 5%;
        bottom: 40%;
        transform: rotate(20deg);
        position: absolute;
      }
      h1 {
        font-size: 2.7rem;
        line-height: 50px;
        margin-top: 33rem;
      }
    }

    @media (max-width: 480px) {
      .skateImg {
        width: 55%;
        right: 10%;
        bottom: 40%;
        transform: rotate(20deg);
        position: absolute;
      }

      h1 {
        font-size: 2.5rem;
        line-height: 50px;
        margin-top: 30rem;
      }
    }
  }
  // 運動不僅僅是一項運動
  .sectionTwo {
    position: relative;
    width: 100%;
    .imageHalf {
      display: flex;

      img {
        width: 100%;
        height: 70dvh;
        object-fit: cover;
      }

      .people1 {
        width: 100%;
      }
      .people2 {
        width: 69vw;
      }
    }

    .sectionTwoDetails {
      width: 50%;
      position: absolute;
      top: 79%;
      right: 0.4%;

      h2 {
        line-height: 110px;
        font-weight: bold;
        font-size: 6.5rem;
        margin: 0;
        color: #b3e837;
      }
      p {
        font-size: 1.2rem;
        width: 40ch;
        line-height: 40px;
        margin-top: 50px;
      }
    }
    .cuteImg {
      position: absolute;
      bottom: -90%;
      left: -3%;
      transform: rotate(25deg);
      width: 500px;
      height: 500px;
      object-fit: cover;
    }

    //RWD1200px
    @media (max-width: 1200px) {
      .sectionTwoDetails {
        width: 50%;
        position: absolute;
        top: 83.5%;
        right: 0.4%;

        h2 {
          line-height: 90px;
          font-weight: bold;
          font-size: 5rem;
          margin: 0;
          color: #b3e837;
        }
        p {
          font-size: 1.1rem;
          width: 40ch;
          line-height: 30px;
          margin-top: 50px;
        }
      }
      .cuteImg {
        position: absolute;
        bottom: -70%;
        left: -3%;
        transform: rotate(25deg);
        width: 400px;
        height: 400px;
        object-fit: cover;
      }
    }

    //RWD767px
    @media (max-width: 767px) {
      .sectionTwoDetails {
        width: 50%;
        position: absolute;
        top: 90.5%;
        right: 0.4%;

        .parallax-window {
          min-height: 200px;
          width: 50%;
          background: transparent;
        }

        h2 {
          line-height: 50px;
          font-weight: bold;
          font-size: 3rem;
          margin: 0;
          color: #b3e837;
        }
        p {
          font-size: 0.8rem;
          width: 40ch;
          line-height: 25px;
          margin-top: 30px;
        }
      }
      .cuteImg {
        position: absolute;
        bottom: -45%;
        left: -3%;
        transform: rotate(25deg);
        width: 250px;
        height: 250px;
        object-fit: cover;
      }
    }

    // RWD480px
    @media (max-width: 480px) {
      .sectionTwoDetails {
        width: 50%;
        position: absolute;
        top: 90.5%;
        right: 0.4%;

        .parallax-window {
          background: transparent;
        }

        h2 {
          line-height: 50px;
          font-weight: bold;
          font-size: 3rem;
          margin: 0;
          color: #b3e837;
        }
        p {
          font-size: 0.8rem;
          width: 30ch;
          line-height: 20px;
          margin-top: 30px;
        }
      }
      .cuteImg {
        position: absolute;
        bottom: -33%;
        left: -3%;
        transform: rotate(25deg);
        width: 200px;
        height: 200px;
        object-fit: cover;
      }
    }
  }

  // 跑馬燈
  .marquee-text {
    overflow: hidden;
    overflow-y: hidden;
    width: 100%;
    position: sticky;
    top: 10%;
    transform: rotate(-3deg);
    z-index: -999;
    // 跑馬燈文字調整
    .marquee {
      p {
        color: transparent;
        -webkit-text-stroke: 2px rgba(168, 57, 255, 1);
        font-size: 10rem;
        font-weight: bold;
      }

      // rwd
      @media (max-width: 1200px) {
        p {
          color: transparent;
          -webkit-text-stroke: 2px rgba(168, 57, 255, 1);
          font-size: 8rem;
          font-weight: bold;
        }
      }

      @media (max-width: 768px) {
        p {
          color: transparent;
          -webkit-text-stroke: 2px rgba(168, 57, 255, 1);
          font-size: 6rem;
          font-weight: bold;
        }
      }

      @media (max-width: 480px) {
        p {
          color: transparent;
          -webkit-text-stroke: 2px rgba(168, 57, 255, 1);
          // color: #a839ff;
          font-size: 4rem;

          font-weight: bold;
        }
      }
    }
  }

  // 挑戰與技巧
  .community {
    position: relative;
    width: 100%;
    height: 100vh;
    text-align: center;
    margin-top: 40rem;

    h2 {
      color: #b3e837;
      font-size: 9rem;
      font-weight: bold;
      line-height: 150px;
      font-weight: bold;
      margin: 0;
    }

    p {
      display: block;
      margin: 0 auto;
      margin-top: 50px;
      width: 50ch;
      color: #fff;
      line-height: 40px;
      font-size: 1.2rem;
    }

    // 噴漆可愛圖片
    .cuteImgTwo {
      overflow: hidden;
      width: 500px;
      position: absolute;
      top: -20%;
      right: -5%;
      transform: rotate(-15deg);
      z-index: -1;
    }
    // 噴漆文字
    .challenge {
      transform: rotate(-20deg);
      position: absolute;
      top: -15%;
      left: 25%;
      width: 25%;
    }
    //rwd 1200px
    @media (max-width: 1200px) {
      h2 {
        color: #b3e837;
        font-size: 7rem;
        font-weight: bold;
        line-height: 120px;
        font-weight: bold;
        margin: 0;
      }

      p {
        display: block;
        margin: 0 auto;
        margin-top: 50px;
        width: 50ch;
        color: #fff;
        line-height: 30px;
        font-size: 1rem;
      }

      // 噴漆可愛圖片
      .cuteImgTwo {
        overflow: hidden;
        width: 400px;
        position: absolute;
        top: -20%;
        right: -5%;
        transform: rotate(-15deg);
        z-index: -1;
      }
      // 噴漆文字
      .challenge {
        transform: rotate(-20deg);
        position: absolute;
        top: -10%;
        left: 25%;
        width: 25%;
      }
    }
    // rwd 768px
    @media (max-width: 767px) {
      h2 {
        color: #b3e837;
        font-size: 4.5rem;
        font-weight: bold;
        line-height: 80px;
        font-weight: bold;
        margin: 0;
      }

      p {
        display: block;
        margin: 0 auto;
        margin-top: 50px;
        width: 50ch;
        color: #fff;
        line-height: 25px;
        font-size: 0.9rem;
      }

      // 噴漆可愛圖片
      .cuteImgTwo {
        overflow: hidden;
        width: 250px;
        position: absolute;
        top: -20%;
        right: -5%;
        transform: rotate(-15deg);
        z-index: -1;
      }
      // 噴漆文字
      .challenge {
        transform: rotate(-20deg);
        position: absolute;
        top: -10%;
        left: 25%;
        width: 25%;
      }
    }

    @media (max-width: 480px) {
      h2 {
        color: #b3e837;
        font-size: 4rem;
        font-weight: bold;
        line-height: 80px;
        font-weight: bold;
        margin: 0;
      }

      p {
        display: block;
        margin: 0 auto;
        margin-top: 50px;
        width: 50ch;
        color: #fff;
        line-height: 20px;
        font-size: 0.8rem;
      }

      // 噴漆可愛圖片
      .cuteImgTwo {
        overflow: hidden;
        width: 200px;
        position: absolute;
        top: -10%;
        right: -5%;
        transform: rotate(-15deg);
        z-index: -1;
      }
      // 噴漆文字
      .challenge {
        transform: rotate(-20deg);
        position: absolute;
        top: -10%;
        left: 25%;
        width: 25%;
      }
    }
  }

  // 右方照片我們是極限挑戰者介紹
  .sustain-right {
    position: relative;
    display: flex;
    justify-content: end;
    width: 100%;
    z-index: 999;
    .parallax-window {
      z-index: 999;
      width: 300px;
      height: 100px;
      margin-right: 10rem;
      object-position: center;
    }
    .sustain-right-text {
      position: absolute;
      top: 79.5%;
      h2 {
        font-size: 6.5rem;
        line-height: 110px;
        color: #b3e837;
        margin: 0px;
      }
      p {
        margin-top: 50px;
        width: 40ch;
        font-size: 1.2rem;
        line-height: 40px;
      }
    }
    .lightning {
      width: 10%;
      position: absolute;
      bottom: 85%;
      right: 10%;
      transform: rotate(10deg);
    }

    @media (max-width: 1200px) {
      .parallax-window {
        z-index: 100;
        width: 30dvw;
        height: 25dvh;
        margin-right: 5rem;
        object-position: center;
      }
      .sustain-right-text {
        position: absolute;
        top: 82.5%;
        h2 {
          font-size: 5.5rem;
          line-height: 90px;
          color: #b3e837;
          margin: 0px;
        }
        p {
          margin-top: 50px;
          width: 40ch;
          font-size: 1rem;
          line-height: 30px;
        }
      }
      .lightning {
        width: 10%;
        position: absolute;
        bottom: 85%;
        right: 5%;
        transform: rotate(10deg);
      }
    }

    @media (max-width: 768px) {
      .parallax-window {
        z-index: 100;
        width: 30dvw;
        height: 25dvh;
        margin-right: 5rem;
        object-position: center;
      }
      .sustain-right-text {
        position: absolute;
        top: 89.5%;
        h2 {
          font-size: 3.5rem;
          line-height: 55px;
          color: #b3e837;
          margin: 0px;
        }
        p {
          margin-top: 50px;
          width: 30ch;
          font-size: 1rem;
          line-height: 30px;
        }
      }
      .lightning {
        width: 10%;
        position: absolute;
        bottom: 85%;
        right: 5%;
        transform: rotate(10deg);
      }
    }
    // RWD 480px
    @media (max-width: 480px) {
      padding-left: 2rem;
      width: 100%;
      .parallax-window {
        width: 100%;
      }
      .sustain-right-text {
        position: absolute;
        top: 89.5%;
        h2 {
          font-size: 3.5rem;
          line-height: 55px;
          color: #b3e837;
          margin: 0px;
        }
        p {
          margin-top: 50px;
          width: 40ch;
          font-size: 1rem;
          line-height: 30px;
        }
      }
      .lightning {
        width: 15%;
        position: absolute;
        bottom: 95%;
        right: 15%;
        transform: rotate(10deg);
      }
    }
  }

  // 左邊照片我們是極限挑戰者介紹
