    display: flex;
    justify-content: start;
    z-index: 1;
    margin-top: 10rem;
    margin-left: 10rem;

    .parallax-window {
      z-index: 100;
      width: 30dvw;
      height: 20dvh;
      position: relative;
      object-position: center;
    }
    .sustain-left-text {
      position: absolute;
      top: 83%;
      h2 {
        font-size: 5.5rem;
        line-height: 90px;
        color: #b3e837;
        margin: 0px;
      }
      p {
        margin-top: 50px;
        width: 40ch;
        font-size: 1.2rem;
        line-height: 40px;
      }
    }
    .star {
      width: 30%;
      position: absolute;
      top: -15%;
      right: 0%;
      transform: rotate(10deg);
    }

    //rwd
    @media (max-width: 1200px) {
      display: flex;
      justify-content: start;
      z-index: 1;
      margin-top: 10rem;
      margin-left: 5rem;

      .parallax-window {
        z-index: 100;
        width: 30dvw;
        height: 25dvh;
        object-position: center;
      }
      .sustain-left-text {
        position: absolute;
        top: 82%;
        left: 0%;
        h2 {
          font-size: 5.5rem;
          line-height: 100px;
          color: #b3e837;
          margin: 0px;
        }
        p {
