          width: 40ch;
          font-size: 1rem;
          line-height: 30px;
        }
      }
      .star {
        width: 30%;
        position: absolute;
        top: -13%;
        right: 0%;
        transform: rotate(10deg);
      }
    }

    // rwd 768px
    @media (max-width: 768px) {
      display: flex;
      justify-content: start;
      z-index: 1;

      .parallax-window {
        width: 40%;
      }
      .sustain-left-text {
        position: absolute;
        top: 89%;
        left: 0%;
        h2 {
          font-size: 3.5rem;
          line-height: 55px;
          color: #b3e837;
          margin: 0px;
        }
        p {
          margin-top: 50px;
          width: 40ch;
          font-size: 1rem;
          line-height: 30px;
        }
      }
      .star {
        width: 30%;
        position: absolute;
        top: -7%;
        right: 0%;
        transform: rotate(10deg);
      }
    }
    // RWD 480px
    @media (max-width: 480px) {
      padding-top: 15rem;
      z-index: 1;

      width: 100%;
      .parallax-window {
        padding-right: 10rem;
        width: 100%;
        height: 5dvh;
      }
      .sustain-left-text {
        position: absolute;
        top: 89%;
        left: 0%;
        h2 {
          font-size: 3.5rem;
          line-height: 55px;
          color: #b3e837;
          margin: 0px;
        }
        p {
          margin-top: 50px;
          width: 30ch;
          font-size: 1rem;
          line-height: 30px;
        }
      }
      .star {
        width: 15%;
        position: absolute;
        top: -7%;
        right: 20%;
        transform: rotate(10deg);
      }
    }
  }

  // 製作疊起來的效果
  #pinContainer {
    width: 100%;
    background-color: #222;
    .panel {
      width: 100%;
      position: relative;
      text-align: center;
      img {
        width: 80%;
      }
    }

    .one-img {
      position: absolute;
      width: 100%;
      padding-left: 50px;

      img {
        width: 55%;
      }
    }
    .two-img {
      background-color: transparent;
      position: absolute;
      width: 100%;
    }
    .three-img {
      background-color: transparent;
      position: absolute;
    }
    .four-img {
      background-color: transparent;
      position: absolute;
    }
  }

  // 底下最後
  .end {
    width: 100%;
    position: relative;

    h2 {
      color: #b3e837;
      font-size: 8rem;
      list-style: 110px;
      line-height: 130px;
      padding-left: 10rem;
      margin-top: 400px;
      padding-bottom: 30px;
    }
    img {
      width: 30%;
      position: absolute;
      right: 5%;
      top: -50%;
      transform: rotate(10deg);
    }

    @media (max-width: 1200px) {
      width: 100%;
      position: relative;

      h2 {
        color: #b3e837;
        font-size: 6rem;
        list-style: 110px;
        line-height: 130px;
        padding-left: 10rem;
        margin-top: 400px;
        padding-bottom: 30px;
      }
      img {
        width: 30%;
        position: absolute;
        right: 12%;
        top: -50%;
        transform: rotate(10deg);
      }
    }

    @media (max-width: 768px) {
      width: 100%;
      position: relative;

      h2 {
        color: #b3e837;
        font-size: 4rem;
        list-style: 110px;
        line-height: 130px;
        padding-left: 10rem;
        margin-top: 400px;
        padding-bottom: 30px;
      }
      img {
        width: 40%;
        position: absolute;
        right: 5%;
        top: -40%;
        transform: rotate(10deg);
      }
    }

    @media (max-width: 480px) {
      width: 100%;
      position: relative;

      h2 {
        color: #b3e837;
        font-size: 2rem;
        line-height: 40px;
        padding-left: 10rem;
        margin-top: 400px;
        padding-bottom: 30px;
      }
      img {
        width: 40%;
        position: absolute;
        right: 5%;
        top: -40%;
        transform: rotate(10deg);
      }
    }
  }
}
