@import url('basic.css');
@import url('https://cdn.jsdelivr.net/npm/bootstrap-icons@1.11.3/font/bootstrap-icons.min.css');
/* 變量設置 */
$bk1: #1d1d1d;
$bk2: #333;
$gr1: #e9e9e9;
$gr2: #7a7a7a;
$main: #baf13a;
$main2: #98d900;
$side: #a839ff;
$side2: #a839ff;

.cartConfirmation-container {
  .container {
    display: flex;
    justify-content: space-between;

    width: 100%;
    margin: 0 auto;
    box-sizing: border-box;
    // 左邊內容物
    .content-left {
      width: 60%;
      margin-top: 5rem;

      // 商品確認步驟
      .order-steps {
        border-bottom: 2px solid $main;
        padding-bottom: 1rem;
        display: flex;
        justify-content: space-between;
        margin-bottom: 3rem;

        a {
          color: white;
          padding-right: 2rem;
          font-size: 1.6rem;
        }

        .fa-solid {
          font-size: 25px;
        }
      }

      .order-info {
        line-height: 30px;

        h3 {
          font-size: 20px;
        }
      }

      // 顧客資訊
      .customer-info {
        margin-top: 10px;

        label {
          margin-top: 200px;
        }

        input {
          color: #fff;
          width: 100%;
          border: 2px solid $main;
          background-color: $bk2;
          border-radius: 10px;
          padding: 5px;
          margin: 10px 0px;
        }

        #order-note {
          padding-bottom: 9rem;
        }
        #orderRemark {
          padding-bottom: 11rem;
        }
      }
    }

    // 右邊內容物
    .content-right {
      width: 35%;
      border: 1px solid $main;
      border-radius: 10px;
      padding: 0px 40px;

      margin-top: 20px;
      background-color: $bk2;

      .payment-method {
        margin-top: 40px;

        h2 {
          font-size: 2rem;
        }
      }

      .form-group {
        padding-top: 1rem;
        margin-top: 5px;
      }

      // 製作下拉式選單
      .dropdown {
        position: relative;
        display: inline-block;
        width: 100%;

        input {
          width: 100%;
          padding: 3px;
          box-sizing: border-box;
          cursor: pointer;
        }

        .dropdown-content {
          display: none;
          position: absolute;
          background-color: $bk2;
          z-index: 1;
          box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
          width: 100%;

          div {
            padding: 10px;
            cursor: pointer;

            &:hover {
              background-color: $bk1;
            }
          }
        }

        &.active .dropdown-content {
          display: block;
        }
      }

      // 文字框框
      .text-border {
        color: #fff;
        border: 2px solid #fff;
        width: 96%;
        border-radius: 5px;
        margin: 0px;
        margin-top: 2px;
        padding: 5px;
      }

      .invoice-promo-code {
        width: 100%;
        display: flex;
        gap: 30px;
      }

      .total-amount {
        margin-top: 2rem;
        border-top: 2px solid #fff;
        padding-top: 20px;

        p {
          line-height: 30px;

          span {
            float: right;
          }
        }
      }

      // 送出按鈕
      .submit {
        border: 1px solid #fff;
        border-radius: 4px;
        margin-top: 20px;
        margin-bottom: 20px;

        button {
          background-color: transparent;
          border: none;
          color: #fff;
          text-align: center;
          padding: 20px;
          display: block;
          width: 100%;
          cursor: pointer;
          font-size: 16px;
          line-height: 1.5;

          &:hover {
            background-color: #fff;
            color: #98d900;
            font-weight: bold;
            transition: 2s;
          }
        }
      }
    }
  }
}
