body {
  font-family: 'Lato', sans-serif;
  overflow-x: hidden;
}

.banner {
  height: 100vh;
  width: 100vw;
  .ml4 {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    position: relative;
    font-weight: 900;
    font-size: 15em;
    font-family: 'Kanit', sans-serif;
  }
  .ml4 .letters {
    position: absolute;
    margin: auto;
    left: 0;
    top: 0em;
    right: 0;
    opacity: 0;
    z-index: -1000;
  }
  .letters {
    overflow: hidden;
    img {
      position: relative;
      height: 100vh;
      width: 100vw;
      opacity: 0.5;
    }

    p {
      position: absolute;
      bottom: 0%;
      left: 35%;
      color: #baf13a;
    }
  }
}
.Dance-container {
  z-index: 10;
  width: 100vw;
  height: 100vh;
  background-color: #1d1d1d;
}

