@charset "UTF-8";
@import url("https://fonts.googleapis.com/css2?family=Just+Another+Hand&display=swap");
html {
  scroll-behavior: smooth;
}

body {
  overflow-x: hidden;
}

.IndexPage {
  /*--------- ontop 按鈕 ---------*/
  /*--------- section 按鈕 ---------*/
}
.IndexPage .content {
  color: white;
  font-family: "Noto Sans TC";
  font-style: normal;
  font-size: 16px;
  font-weight: 500;
  line-height: 38px;
  letter-spacing: 1.5px;
  margin-top: 20px;
  text-align: justify;
}
.IndexPage .read_moreBtn {
  position: absolute;
  right: -40px;
  top: 50%;
  bottom: 50%;
  display: inline-block;
  cursor: pointer;
  outline: none;
  border: 0;
  vertical-align: middle;
  text-decoration: none;
  background: transparent;
  padding-right: 20px;
  font-size: 14px;
  font-weight: 500;
  width: 10rem;
}
.IndexPage .read_moreBtn .circle {
  transition: all 0.45s cubic-bezier(0.65, 0, 0.076, 1);
  position: relative;
  display: block;
  margin: 0;
  width: 26px;
  height: 26px;
  background: var(--main);
  border-radius: 1.625rem;
}
.IndexPage .read_moreBtn .circle .icon {
  transition: all 0.45s cubic-bezier(0.65, 0, 0.076, 1);
  position: absolute;
  top: 0;
  bottom: 0;
  margin: auto;
  background: var(--bk1);
}
.IndexPage .read_moreBtn .circle .arrow {
  transition: all 0.45s cubic-bezier(0.65, 0, 0.076, 1);
  left: 0.5px;
  width: 1.125rem;
  height: 0.125rem;
  background: none;
}
.IndexPage .read_moreBtn .circle .arrow::before {
  position: absolute;
  content: "";
  top: -0.25rem;
  right: 0.1rem;
  width: 0.5rem;
  height: 0.5rem;
  border-top: 0.15rem solid var(--bk1);
  border-right: 0.15rem solid var(--bk1);
  transform: rotate(45deg);
}
.IndexPage .read_moreBtn .button-text {
  transition: all 0.45s cubic-bezier(0.65, 0, 0.076, 1);
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  padding: 0.1rem 0;
  color: white;
  line-height: 1.7;
  text-align: center;
  text-transform: uppercase;
}
.IndexPage .read_moreBtn:hover .circle {
  width: 100%;
}
.IndexPage .read_moreBtn:hover .circle .icon.arrow {
  background: var(--bk1);
  transform: translate(1rem, 0);
}
.IndexPage .read_moreBtn:hover .button-text {
  color: var(--bk1);
}
.IndexPage .title_sub {
  color: white;
  font-family: "Noto Sans TC";
  font-style: normal;
  font-size: 16px;
  font-weight: 900;
  line-height: 38px;
}
.IndexPage .starbg {
  position: fixed;
  width: 100%;
  top: 0;
  right: 0;
  z-index: -99;
}
