@import url('basic.css');
@import url('https://cdn.jsdelivr.net/npm/bootstrap-icons@1.11.3/font/bootstrap-icons.min.css');

/* 變量設置 */
$bk1: #1d1d1d;
$bk2: #333;
$gr1: #e9e9e9;
$gr2: #7a7a7a;
$main: #baf13a;
$main2: #98d900;
$side: #a839ff;
$side2: #a839ff;

.cartCheckOut {
  .container {
    display: flex;
    justify-content: space-between;

    width: 100%;
    margin: 0 auto;
    box-sizing: border-box;

    @media (max-width: 768px) {
      padding: 2rem;
      flex-direction: column;
    }

    .content-left {
      width: 60%;
      margin-top: 5rem;

      // 商品確認步驟
      .order-steps {
        border-bottom: 2px solid $main;
        padding-bottom: 1rem;
        display: flex;
        justify-content: space-between;
        margin-bottom: 3rem;

        a {
          color: white;
          padding-right: 2rem;
          font-size: 1.6rem;
        }

        .fa-solid {
          font-size: 25px;
        }
      }

      .order-info {
        line-height: 30px;

        h3 {
          font-size: 20px;
        }
      }

      // 商品項目調整
      .cart-items {
        display: flex;
        align-items: center;
        margin-top: 30px;
        border: 2px solid $main;
        background-color: $bk2;
        padding: 12px;
        border-radius: 20px;

        img {
          width: 130px;
          height: 130px;
          object-fit: cover;
        }

        .cart-item-content {
          width: 25%;
          margin-left: 20px;
          line-height: 30px;

          h4 {
            font-size: 1.2rem;
          }
        }
        .quantity-plus-minus {
          margin: 0;
          width: 10%;
          display: flex;
          align-items: center;
          gap: 15px;
          margin-right: 6rem;
          margin-left: 6rem;
          pointer-events: none;

          .quantity {
            font-size: 1.3rem;
          }

          .fa-solid {
            color: #fff;
          }
          .fa-solid:hover {
            color: $main;
            transition: 1s;
          }
        }
        .total {
          font-size: 1.3rem;
        }
        .minus-button,
        .plus-button {
          background: none;
          border: none;
          outline: none;
          color: inherit;
          font: inherit;
          cursor: pointer;
        }
      }

      // 顧客資訊
      .customer-info {
        margin-top: 10px;

        label {
          margin-top: 200px;
        }

        input {
          color: #fff;
          width: 100%;
          border: 2px solid $main;
          background-color: $bk2;
          border-radius: 10px;
          padding: 5px;
          margin: 10px 0px;
        }

        #order-note {
          padding-bottom: 9rem;
        }
      }
    }

    // 右邊內容物
    .content-right {
      width: 35%;
      position: sticky;
      top: 0;

      border: 1px solid $main;
      border-radius: 10px;
      margin-top: 80px;
      padding: 0px 40px;
      background-color: $bk2;

      .payment-method {
        margin-top: 40px;

        h2 {
          font-size: 2rem;
        }
      }

      .form-group {
        padding-top: 1rem;
        margin-top: 5px;
      }

      // 製作下拉式選單
      .dropdown {
        position: relative;
        display: inline-block;
        width: 100%;

        input {
          width: 100%;
          padding: 3px;
          box-sizing: border-box;
          cursor: pointer;
        }

        .dropdown-content {
          display: block;
          position: absolute;
          background-color: $bk2;
          z-index: 999;
          box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
          width: 100%;

          div {
            padding: 10px;
            cursor: pointer;

            &:hover {
              background-color: $bk1;
            }
          }
        }
      }

      // 文字框框
      .text-border {
        color: #fff;
        border: 2px solid #fff;
        width: 100%;
        border-radius: 5px;
        margin: 0px;
        margin-top: 2px;
        padding: 5px;
      }

      .invoice-promo-code {
        width: 100%;
        display: flex;
        gap: 10px;
      }

      .total-amount {
        margin-top: 2rem;
        border-top: 2px solid #fff;
        padding-top: 20px;

        p {
          line-height: 30px;

          span {
            float: right;
          }
        }
        #home-address {
          color: #fff;
        }
      }

      // 公司行號輸入框

      // 送出按鈕
      .submit {
        border: 1px solid #fff;
        border-radius: 4px;
        margin-top: 20px;
        margin-bottom: 20px;

        button {
          background-color: transparent;
          border: none;
          color: #fff;
          text-align: center;
          padding: 20px;
          display: block;
          width: 100%;
          cursor: pointer;
          font-size: 16px;
          line-height: 1.5;

          &:hover {
            background-color: #fff;
            color: #98d900; /* 原本 $main2 的色碼 */
            font-weight: bold;
            transition: 2s;
          }
        }
      }
    }
  }
}
