@import url('basic.css');

/* 變量設置 */
$bk1: #1d1d1d;
$bk2: #333;
$gr1: #e9e9e9;
$gr2: #7a7a7a;
$main: #baf13a;
$main2: #98d900;
$side: #a839ff;
$side2: #a839ff;

.wrap-f .container-f {
  // 輪播圖調整
  .swiper {
    width: 100%;
    height: 600px;
    .swiper-slide {
      position: relative;

      img {
        width: 100%;
        height: 600px;
        object-fit: cover;
        opacity: 0.6;
        object-position: center;
      }

      .swiper-text {
        width: 40%;
        position: absolute;
        top: 50%;
        left: 70%;
        transform: translate(-50%, -50%);
        color: #fff;
        text-align: center;

        h1 {
          line-height: 3rem;
          font-size: 3rem;
          font-weight: bold;
          padding-bottom: 2rem;
        }

        p {
          font-size: 1.1rem;
          font-weight: 400;
          line-height: 1.5rem;
        }
      }
    }
  }
  // 輪播圖RWD
  @media (max-width: 1200px) {
    .swiper {
      height: 400px;
      .swiper-slide {
        img {
          width: 100%;
          height: 400px;
          object-fit: cover;
          opacity: 0.6;
          object-position: center;
        }
      }
    }
  }
  // 輪播圖RWD
  @media (max-width: 768px) {
    .swiper {
      height: 350px;
      .swiper-slide {
        img {
          width: 100%;
          height: 350px;
          object-fit: cover;
          opacity: 0.6;
          object-position: center;
        }
        .swiper-text {
          h1 {
            line-height: 1rem;
            font-size: 2rem;
            font-weight: bold;
            padding-bottom: 2rem;
          }

          p {
            font-size: 0.9rem;
            font-weight: 400;
          }
        }
      }
    }
  }
  // 輪播圖RWD
  @media (max-width: 576px) {
    .swiper {
      height: 300px;
      .swiper-slide {
        img {
          width: 100%;
          height: 300px;
          object-fit: cover;
          opacity: 0.6;
          object-position: center;
        }
        .swiper-text {
          h1 {
            line-height: 1rem;
            font-size: 1.5rem;
            font-weight: bold;
            padding-bottom: 1.5rem;
          }

          p {
            font-size: 0.8rem;
            font-weight: 400;
          }
        }
      }
    }
  }
}

// 下方未滿版區域
.AllWrap .container {
  margin: auto;
  margin-top: 8rem;
  display: flex;
  position: relative;

  // 左邊各項商品篩選
  // 外層
  .shop-filter {
    margin-right: 50px;
    position: sticky;
    top: 10px;
    padding: 10px 20px;
    width: 20%;
    height: 80%;
    border: 0.75px solid var(--main2);
    border-radius: 20px;

    .filter-text {
      padding-bottom: 20px;
      padding-top: 20px;
    }

    h3 {
      font-size: 1.2rem;
      padding-bottom: 10px;
      color: $main;
    }

    input {
      width: 100%;
      border-bottom: 0.75px solid var(--main2);
      margin: 0px;
    }

    ul li {
      font-size: 1.1rem;
      font-weight: bold;
      display: inline-block;
      margin-top: 1rem;
      margin-bottom: 0.5rem;
      width: 200px;

      a {
        display: inline-block;
        padding: 10px 10px 10px 10px;
        border-radius: 20px;

        &:hover {
          background-color: $main;
          color: $bk2;
        }

        &.selected {
          background-color: $main;
          color: $bk1;
        }
      }
    }
  }

  // 響應式768專用搜尋欄位
  // 左邊各項商品篩選
  .RWD-shop-filter {
    position: absolute;
    margin-right: 50px;
    padding: 7px 17px;
    width: 85%;
    height: 7%;
    border: 0.75px solid var(--main2);
    border-radius: 20px;
    white-space: nowrap;
    display: none;

    #categoryFilter {
      gap: 10px;
      display: flex;
    }

    ul li {
      font-size: 0.7rem;
      font-weight: 500;
      display: inline-block;
      margin-top: 0.5rem;

      a {
        display: inline-block;
        padding: 5px;
        border-radius: 20px;

        &:hover {
          background-color: $main;
          color: $bk2;
        }

        &.selected {
          background-color: $main;
          color: $bk1;
        }
      }
    }
  }
  // 左側篩選響應式
  @media (max-width: 768px) {
    .shop-filter {
      display: none;
    }
    .RWD-shop-filter {
      top: -5%;
      height: 2%;
      display: block;
    }
  }
  @media (max-width: 576px) {
    .RWD-shop-filter {
      flex-wrap: wrap;
      top: -3%;
      height: 1.7%;
      width: auto;

      #categoryFilter {
        display: flex;
        gap: 0px;
      }

      ul li {
        font-size: 0.5rem;
        font-weight: 500;
        display: inline-block;
        margin-top: 0.5rem;

        a {
          display: inline-block;
          padding: 5px;
          border-radius: 20px;

          &:hover {
            background-color: $main;
            color: $bk2;
          }

          &.selected {
            background-color: $main;
            color: $bk1;
          }
        }
      }
    }
  }

  // 右邊商品顯示區域
  .product-list {
    width: 80%;
    padding: 20px 20px 20px 30px;
    display: flex;
    background-color: #333;
    border-radius: 20px;
    flex-wrap: wrap;
    overflow-y: auto;
    height: 100%;

    // 商品欄
    .product-item {
      flex-basis: 30%;
      min-width: 200px;
      height: 30%;
      margin: 1.5%;
      border-radius: 0px 0px 20px 20px;
      box-shadow: 2px 3px 3px 3px rgba(0, 0, 0, 0.1);
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      flex-wrap: wrap;
      background-color: white;
    }

    // 售完商品用js去新增使用
    .sold-out-card {
      position: relative;
      pointer-events: none;

      // 使用before 填滿 不會影響到sold out貼條變暗
      &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(0, 0, 0, 0.5);
        border-radius: 0px 0px 10px 10px;
        z-index: 1;
      }
    }

    .product-img {
      width: 100%;
      height: 200px;
      object-fit: cover;
      position: relative;

      img {
        height: 200px;
        object-fit: cover;
        background-color: $bk1;
      }

      // 售完的貼條
      .sold-out {
        position: absolute;
        width: 100%;
        height: 20%;
        top: 60%;
        left: 50%;
        transform: translate(-50%, -50%) rotate(-25deg);
        background-color: rgba(130, 174, 40, 0.8);
        color: #000;
        font-size: 2.5rem;
        font-weight: bold;
        padding: 10px 10px;
        padding-top: 30px;
        z-index: 10;
        text-align: center;
        pointer-events: none;
        opacity: 1;
        white-space: nowrap;
      }
    }
    .fa-regular {
      position: absolute;
      bottom: 10%;
      right: 7%;
      color: #fff;
      font-size: 0.9rem;
      border: 1px solid #fff;
      border-radius: 50%;
      padding: 5px;
      transition: 1s;

      &.selected {
        background-color: crimson; /* 點擊後背景變為紅色 */
        color: #fff; /* 愛心內部變為白色 */
      }

      &:hover {
        background-color: crimson;
        color: #fff;
      }
    }

    .labels {
      padding-top: 10px;
      padding-left: 10px;
      gap: 10px;
      display: flex;
      align-items: center;
      color: black;
      font-size: 0.6rem;

      span {
        text-align: center;
        padding: 1.5px 15px;
        border: 1px solid black;
        border-radius: 10px;

        // &.normal { }
        // 新品橙色
        &.sale {
          background-color: #ff5722;
        }
        // 優惠黃色
        &.new {
          background-color: #ffc107;
        }
      }
    }

    // 商品文字
    .product-info {
      color: black;
      padding-left: 15px;
      padding-top: 15px;
      padding-bottom: 20px;
      background-color: #fff;
      border-radius: 0px 0px 15px 15px;
    }

    h4 {
      font-weight: bolder;
    }
    .product-text {
      position: relative;
      display: flex;
      justify-content: space-between;
      text-align: center;
      padding-top: 40px;

      p {
        font-size: 18px;
        font-weight: bold;
      }

      // 優惠折扣
      .discount-price {
        position: absolute;
        bottom: 30px;
        left: 0px;
        font-size: 1.4rem;
        color: #ff0000;
        font-weight: bold;
      }

      // 查看按鈕
      .look-btn {
        color: #333;
        background-color: var(--main2);
        border-radius: 20px;
        font-size: 11px;
        font-weight: bold;
        padding: 10px;
        margin-right: 10px;
        text-align: center;
        display: flex;
        align-items: center;
        border: none;
        cursor: pointer;

        &:hover {
          background-color: $main;
          color: var(--bk2);
          padding: 10px;
          border-radius: 20px;
        }
      }
    }

    // 右方響應式1200px
    @media (max-width: 1200px) {
      width: 100%;
      gap: 10px;

      // 商品欄
      .product-item {
        height: 30%;
        margin: 1%;
        flex-grow: 1;
        flex-basis: calc(49.333% - 20px);
        max-width: calc(49.333% - 20px);
      }
    }

    // RWD992px
    @media (max-width: 992px) {
      width: 100%;

      // 商品欄
      .product-item {
        flex-basis: 5%;
        min-width: 210px;
        height: 30%;
        margin: 1%;
      }
    }

    // 768px
    @media (max-width: 768px) {
      width: 100%;
      margin-left: 2%;
      .product-item {
        flex-basis: 45%;
        min-width: 200px;
        height: 30%;
        margin: 1.5%;
      }
    }

    @media (max-width: 576px) {
      margin: auto;
      width: 90%;
      height: 50%;
      .product-item {
        transform: scale(0.93);
        flex-basis: 100%;
        max-width: 50%;

        margin: 1.5% auto;
        flex-basis: calc(100% - 20px);
        max-width: calc(100% - 20px);
      }
      // 商品文字
      .product-info {
        color: black;
        padding-left: 15px;
        padding-top: 15px;
        padding-bottom: 20px;
        background-color: #fff;
        border-radius: 0px 0px 15px 15px;
      }

      h4 {
        font-weight: bolder;
        font-size: 1.3rem;
      }
      .product-text {
        position: relative;
        display: flex;
        justify-content: space-between;
        text-align: center;
        padding-top: 40px;

        p {
          font-size: 1.2rem;
          font-weight: bold;
        }

        // 優惠折扣
        .discount-price {
          position: absolute;
          bottom: 30px;
          font-size: 1.4rem;
          color: #ff0000;
          font-weight: bold;
        }

        // 查看按鈕
        .look-btn {
          color: #333;
          background-color: var(--main2);
          border-radius: 20px;
          font-size: 1rem;
          font-weight: bold;
          padding: 10px;
          margin-right: 10px;
          text-align: center;
          display: flex;
          align-items: center;
          border: none;
          cursor: pointer;

          &:hover {
            background-color: $main;
            color: var(--bk2);
            padding: 10px;
            border-radius: 20px;
          }
        }
      }
    }
  }
}
